@import "helpers/variables";
@import "helpers/mixins";
@import "helpers/functions";

@import "vendor/swiper-bundle.scss";

@font-face {
  font-family: 'Montserrat';
  src: url('/fonts/subset-Montserrat-ExtraBold.woff2') format('woff2');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

.detailBlock{
  .section-h2 {
    font-family: 'Montserrat', sans-serif;
  }
}

.services {
  padding: 20px 0 70px 0;

  .container {
    position: relative;
  }

  .tabs__toggle{
    @media screen and (min-width: 480px) {
      padding: 14px 18px;
    }
    @media screen and (min-width: 640px) {
      font-size: 16px;
      padding: 19px 25px;
    }
    @media screen and (min-width: 1152px) {
      font-size: 18px;
    }
  }

  .tabs__toggles{
    margin-bottom: 20px;
  }

  &-items{
    display: flex;
    flex-wrap: wrap;
    margin: 0 -10px;

    &__item{
      width: 100%;
      margin: 10px;
      position: relative;
      border-radius: 10px;
      overflow: hidden;

      @media screen and (min-width: 640px) {
        width: calc(50% - 20px);
      }

      @media screen and (min-width: 960px) {
        width: calc(50% - 40px);
        margin: 20px;
        border-radius: 20px;
      }

    }

    &__image{
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    &__content{
      position: relative;
      z-index: 1;
      background: rgba(26, 6, 67, 0.35);
      padding: 15px 10px 10px;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      min-height: 188px;

      @media screen and (min-width: 480px) {
        padding: 20px;
      }

      @media screen and (min-width: 576px) {
        min-height: 250px;
      }

      @media screen and (min-width: 960px){
        min-height: 300px;
      }

      @media screen and (min-width: 1152px){
        min-height: 350px;
        padding: 30px 20px;
      }


      .title{
        font-weight: 700;
        font-size: 16px;
        line-height: 130%;
        letter-spacing: 0.05em;
        color: #FFFFFF;
        margin-bottom: 7px;

        @media screen and (min-width: 640px) {
          font-size: 20px;
        }

        @media screen and (min-width: 1152px) {
          font-size: 25px;
        }

      }

      .price{
        font-weight: 700;
        font-size: 10px;
        line-height: 150%;
        color: #FFFFFF;
        margin-bottom: 57px;

        span{
          margin-left: 10px;
          font-size: 16px;
          line-height: 150%;
        }

        @media screen and (min-width: 640px) {
          font-size: 14px;

          span{
            font-size: 24px;
          }

        }

        @media screen and (min-width: 1152px) {
          font-size: 17px;

          span{
            font-size: 32px;
          }

        }

      }

      .btn{
        margin-top: auto;
        color: #8C52FF;
        padding: 10px 19px;

        @media screen and (min-width: 640px) {
          padding: 11px 24px;
          font-size: 15px;
        }

        @media screen and (min-width: 1152px) {
          padding: 14px 32px;
          font-size: 17px;
          line-height: 23px;
        }

      }

    }

  }

  &-figure {
    position: absolute;
    display: none;

    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (min-width: 480px) {
    }

    @media screen and (min-width: 640px) {
    }

    @media screen and (min-width: 960px) {
      display: block;
      width: 421px;
      height: 360px;
      top: -60px;
      right: -96px;
      z-index: -1;
    }

    @media screen and (min-width: 1152px) {
      top: -120px;
      right: -192px;
    }


  }

  @media screen and (min-width: 640px) {
    padding: 40px 0 70px 0;

    .section-h2 {
      margin-bottom: 30px;
    }

  }

  @media screen and (min-width: 830px) {
    padding: 40px 0 90px 0;

  }

  @media screen and (min-width: 1152px) {
    padding: 80px 0 120px 0;

    .section-h2 {
      margin-bottom: 70px;
    }

  }


  .section-h2 {
    margin-bottom: 20px;
    width: 100%;
    text-align: center;
  }

  @media screen and (min-width: 480px) {


  }

  @media screen and (min-width: 960px) {

    .section-h2 {
      text-align: left;
    }

  }

  @media screen and (min-width: 1152px) {

  }

}

